.callout{
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    padding-top: 50px;
    padding-bottom: 50px;
    position: relative;
    > *{
        position: relative;
        z-index: 1;
    }
    &:after{
        content: '';
        position: absolute;
        background: $havelock-blue;
        width: 100%;
        height: 100%;
        opacity: 0.5;
        top:0;
        left: 0;
    };
}