.header{
    position: fixed;
    z-index: 10;
    background-color: rgba(0,0,0,.9);
    .navbar{
        .navbar-toggler{
            display: block;
            width: 60px;
            height: 45px;
            position: relative;
            margin:10px 0 10px auto;
            -webkit-transform: rotate(0deg);
            -moz-transform: rotate(0deg);
            -o-transform: rotate(0deg);
            transform: rotate(0deg);
            -webkit-transition: .5s ease-in-out;
            -moz-transition: .5s ease-in-out;
            -o-transition: .5s ease-in-out;
            transition: .5s ease-in-out;
            cursor: pointer;
            outline: 0;
            span{
                display: block;
                position: absolute;
                height: 5px;
                width: 100%;
                background: $white;
                border-radius: 9px;
                opacity: 1;
                left: 0;
                -webkit-transform: rotate(0deg);
                -moz-transform: rotate(0deg);
                -o-transform: rotate(0deg);
                transform: rotate(0deg);
                -webkit-transition: .25s ease-in-out;
                -moz-transition: .25s ease-in-out;
                -o-transition: .25s ease-in-out;
                transition: .25s ease-in-out;
                &:nth-child(1) {
                    top: 0px;
                    -webkit-transform-origin: left center;
                    -moz-transform-origin: left center;
                    -o-transform-origin: left center;
                    transform-origin: left center;
                }
                
                &:nth-child(2) {
                    top: 18px;
                    -webkit-transform-origin: left center;
                    -moz-transform-origin: left center;
                    -o-transform-origin: left center;
                    transform-origin: left center;
                }
                
                &:nth-child(3) {
                    top: 36px;
                    -webkit-transform-origin: left center;
                    -moz-transform-origin: left center;
                    -o-transform-origin: left center;
                    transform-origin: left center;
                }
            }
            
        }
       .navbar-collapse{
           transition: all .5s;
           height: 0px;
           opacity: 0;
           display: flex;
           flex-direction: column;
           justify-content: center;
            align-items: center;
            pointer-events: none;
           ul{
               list-style: none;
               display: flex;
               flex-direction: column;
               padding: 0;
               margin: 0;
               li{
                   margin: 15px 0;
                   text-align: center;
                   a{
                        color: $white;
                        font-size: 40px;
                        font-family: $font-duality;
                   }
               }
           }
           .social-links{
               margin-top: 30px;
               a{
                   color: $white;
                   font-size: 40px;
                   margin: 0 1rem;
               }
           }
           
       }

       &.show{
        .navbar-toggler{
            span{
                &:nth-child(1) {
                    -webkit-transform: rotate(45deg);
                    -moz-transform: rotate(45deg);
                    -o-transform: rotate(45deg);
                    transform: rotate(45deg);
                    top: -3px;
                    left: 8px;
                }
                
                &:nth-child(2) {
                    width: 0%;
                    opacity: 0;
                }
                
                &:nth-child(3) {
                    -webkit-transform: rotate(-45deg);
                    -moz-transform: rotate(-45deg);
                    -o-transform: rotate(-45deg);
                    transform: rotate(-45deg);
                    top: 39px;
                    left: 8px;
                }
            }
        }
        .navbar-collapse{
            height: calc(100vh - 81px);
            opacity: 1;
            pointer-events: visible;
        }
       }
    }
    @include breakpoint('tablet-up'){
        .navbar{
            .navbar-toggler{
                display: none;
            }
            .navbar-collapse{
                opacity: 1;
                height: auto;
                transition: all 0s;
                justify-content: flex-start;
                align-items: flex-start;
                pointer-events: visible;
                ul{
                    flex-direction: row;
                    li{
                        a{
                            font-size: 20px;
                            margin: 0 20px;
                        }
                    }
                }
                .social-links{
                    display: none;
                }
            }
           &.show{
            .navbar-collapse{
                height: auto;
            }
           }
        }
    }
}