@font-face {
    font-family: 'Top_Modern';
    src: url('../../assets/fonts/TopModern.eot');
    src: url('../../assets/fonts/TopModern.eot?#iefix') format('embedded-opentype'),
        url('../../assets/fonts/TopModern.woff2') format('woff2'),
        url('../../assets/fonts/TopModern.woff') format('woff'),
        url('../../assets/fonts/TopModern.ttf') format('truetype'),
        url('../../assets/fonts/TopModern.svg#TopModern') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Duality';
    src: url('../../assets/fonts/Duality-Regular.eot');
    src: url('../../assets/fonts/Duality-Regular.eot?#iefix') format('embedded-opentype'),
        url('../../assets/fonts/Duality-Regular.woff2') format('woff2'),
        url('../../assets/fonts/Duality-Regular.woff') format('woff'),
        url('../../assets/fonts/Duality-Regular.ttf') format('truetype'),
        url('../../assets/fonts/Duality-Regular.svg#Duality-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
}