.services-list{
    padding-top: 2rem;
    padding-bottom: 2rem;
    .service-card{
        width: 100%;
        min-height: 150px;
        border-radius: 0;
        border: none;
        background-color: transparent;
        color: $white;
        align-items: center;
        padding: 20px 0;
        margin-bottom: 20px;
        &.shakespeare{
            background-color: $shakespeare;
        }
        &.olivine{
            background-color: $olivine;
        }
        &.medium-purple{
            background-color: $medium-purple;
        }
        .card-img-top{
            width: 125px;
            margin-bottom: 1rem;
        }
        .card-body{
            padding: 0;
            .card-title{
                font-size: 32px;
                font-family: $font-duality;
            }
            .card-text{
                font-size: 18px;
                font-family: $font-top-modern;
            }
        }
    }
}