footer{
    padding: 20px 0;
    background-color: $cod-gray; 
    .copyright{
        color: $white;
        margin-bottom: 0;
    }
    .footer-links{
        a{
            font-size: 24px;
            text-decoration: none;
            color: $white;
            margin-right: 20px;
            &:last-child{
                margin: 0;
            }
            &:hover{
                text-decoration: none;
            }
        }
    }
}